<template>
  <div class="pdf-demo">
    <el-button class="pdf-print-btn" type="primary" plain @click="goPrint" size="small">打印</el-button>
    <!-- 根据pdf生成canvas -->
    <div class="pdf-list" id="pdf-list">
      <div style="page-break-after: always;" v-for="(item, index) in list" :key="index">
        <h1>第{{index+1}}份pdf</h1>
        <!-- 这是不分页预览 -->
        <div v-for="(item2, index2) in list[index].pageCount" :key="index2">
          <div>第{{index2+1}}页</div>
          <pdf :src="list[index].src" :page="index2+1"></pdf>
        </div>
        <pdf :src="list[index].src" @num-pages="list[index].pageCount=$event" style="display: none;"></pdf>
      </div>
    </div>
    <!-- 根据canvas生成img，=》然后打印 -->
    <div class="print-list-container" style="display: none;">
      <div class="print-list" ref="print">
        <div style="page-break-after: always;" v-for="(item, index) in img_urlList" :key="index">
          <img :src="item" alt=""></img>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import pdf from 'vue-pdf'
  import axios from 'axios'
  import html2canvas from "html2canvas"
  export default {
    components: {
      pdf,
    },
    data() {
      return {
        list: [],
        pageCountList: [],
        img_urlList: []
      }
    },
    created() {},
    mounted() {
      this.list = [{ src: './static/pdfs/test.pdf', pageCount: 1 }, { src: './static/pdfs/test.pdf', pageCount: 1 }]
    },
    methods: {
      goPrint() {
        let canvas = document.querySelectorAll('#pdf-list canvas')
        console.log(canvas)
        for (let i = 0; i < canvas.length; i++) {
          this.img_urlList[i] = canvas[i].toDataURL("image/png")
        }
        this.img_urlList = [...this.img_urlList]
        console.log(this.img_urlList)
        this.$nextTick(() => {
          this.$print(this.$refs.print, {
            'title': 'this is a test title'
          })
        })
        // setTimeout(() => {
        //   this.$print(this.$refs.print, {
        //     'title': 'this is a test title'
        //   })
        // }, 2000)
      },
      handleLoaded(index, value) {
        this.list[index].currentPage++
        // this.list[index].currentPage += value
        console.log(this.list[index].currentPage, this.list[index].pageCount)
        // return () => {
        //   this.list[index].currentPage = 1
        // }
      }
    }
  }
</script>

<style scoped>
  .pdf-demo {
    padding: 50px 20px;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    overflow-x: hidden;
    overflow-y: auto;
  }
  .pdf-print-btn {
    position: fixed;
    right: 0;
    top: 50%;
    z-index: 100;
  }
</style>



